import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Form } from "../pages/form/form";
import { Space } from "../pages/space/space";
import { Billing } from "../pages/billing/billing";
import { Newone } from "../pages/newone/newone";
import { Restaurant } from "../pages/restaurant/restaurant";
import { Movies } from "../pages/movies/movies";
import { Travel } from "../pages/travel/travel";
import { Doctors } from "../pages/doctors/doctors";
import { Health } from "../pages/health/health";
import { Supermarket } from "../pages/supermarket/supermarket";
import { Electrical } from "../pages/electrical/electrical";
import { Industrialsupplies } from "../pages/industrialsupplies/industrialsupplies";
import { Machine } from "../pages/machine/machine";
import { Automobile } from "../pages/automobile/automobile";


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    Form,
    Space,
    Billing,
    Newone,
    Restaurant,
    Movies,
    Travel,
    Doctors,
    Health,
    Supermarket,
    Electrical,
    Industrialsupplies,
    Machine,
    Automobile
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    Form,
    Space,
    Billing,
    Newone,
    Restaurant,
    Movies,
    Travel,
    Doctors,
    Health,
    Supermarket,
    Electrical,
    Industrialsupplies,
    Machine,
    Automobile

  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



